import { Card, CardBody, CardHeader } from "react-bootstrap";
import AssetsTypeCountChart from "../../layout/common/AssetsTypeCountChart";
import React from "react";
import { baseUrl } from "../../../../../utils";

 const PosttypeCount = (props) => {

  const { postTypeCount, post_type_count_loader } = props  

  console.log(postTypeCount[0]['total'],'postTypeCountpostTypeCount')

  return (
    <Card className="sw-card-custom gutter-b card-shadow mb-5">
      <CardHeader className="p-5">
        <h6 class="card-label font-weight-bolder m-0"> Post Type Count</h6>
      </CardHeader>
      <CardBody className="p-5">

          { post_type_count_loader || !postTypeCount[0]['total'] ?  
          <div class="">
          <div class="text-center">
            <div class="analytics_overview_chart">
              <img className="img-fluid" src={baseUrl(`dashboard/analytic/post-count.svg`)}/>
              </div>
          </div>
        </div>  :
           postTypeCount && Object.keys(postTypeCount).length  ? <AssetsTypeCountChart key={post_type_count_loader} {...props}/> : null  }
      </CardBody>
    </Card>
    
  );
};

export default React.memo(PosttypeCount)

